body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
      'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
      sans-serif;
      height: 90vh;
  }
  
  .App {
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 50px 0;
  }
  
  section{
    margin: 2rem 0 1rem 0;
  }
  
  .controls {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  
  input{
    text-align: center;
    padding: 0.5rem;
    margin-bottom: 1rem;
  }
  
  button {
    padding: 0.5rem 1rem;
    background-color: #c92e00;
    color: whitesmoke;
    border: none;
    font-family: "Roboto", sans-serif;
    font-size: 1rem;
  }
  button:hover{
    cursor: pointer;
    background-color: #962401;
  }
  
  main{
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  h1{
    text-align: center;
    margin-bottom: 2rem;
  }
  .mealPlanTitle{
    font-size: 50px;
    margin-top: -20px;
  }
  .nutrients ul{
    display: flex;
    width: 35rem;
    justify-content: space-evenly;
  }
  ul{
    list-style: none;
  
  }
  .meals{
    display: flex;
  }
  img{
    width: 100%;
    margin-bottom: 1rem;
  }
  
  article{
    display: flex;
    flex-direction: column;
    padding: 2rem;
    margin: 0 1rem;
    max-width: 18rem;
    box-shadow: 0 4px 8px 2px rgba(77, 77, 77, 0.15);
  }
  .instructions {
    font-size: 0.9rem;
    margin-bottom: 1rem;
  }
  
  a{
    text-decoration: none;
    background-color: #c92e00;
    color: #f3f3f3;
    width: fit-content;
    padding: 0.5rem 1rem;
  }
  a:hover {
    cursor: pointer;
    background-color: #962401;
  }
  h3{
    margin-top: 30px;
  }
  
  @media only screen and (max-width: 550px) {
    .meals {
      flex-direction: column;
      align-items: center;
    }
  
    .nutrients ul {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
    }
    ul{
      justify-content: start;
      margin-left: -40px;
      padding-left: -40px;
    }
    a{
      display: flex;
      align-items: center;
      justify-content: center;
    }
    h3{
      margin-top: 20px;
      font-size: 12px;
    }
  }
  